* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0px;
    font-family: 'Poppins', sans-serif;
    cursor:url('curr.png'), auto;
    font-family: 'Roboto Flex', sans-serif;
   background: url('https://pbs.twimg.com/media/EQRo75hW4AY5C11?format=jpg&name=900x900');
}

nav {
    display: flex;
    padding: 20px 36px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    background-color: white;
    background: #21222A;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav--logo {
    width: 150px;
    height: 150px;
}

section {
    padding: 20px;
}

.hero {
    display: flex;
    flex-direction: column;
}

.hero--photo {
    max-width: 400px;
    align-self: center;
}

.hero--header {
    margin-bottom: 16px;
}

.hero--text {
    margin-top: 0;
}

.card-container{
    width: 800px;
    /* padding: 10px; */
    /* background-color: chocolate; */
    font-size: 20px;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border: 4px dashed black;
    background-color: blueviolet;
    margin: 50px;
    border-radius: 10px;
}

.info-plan {
    display: none;
}

.card-container img {
    border-radius: 15px;
    width: 100%;
}

.bold {
    font-weight: bold;
}

.small-data-container{
    font-size: 2rem;
}

.activity{
    font-style: italic;
    font-weight: bold;
    text-decoration: dashed;
    text-align: center;
}

.events{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin-top: 40px;
}

.events h1{
    background-color: white;
    border: 15px double red;
    color: black;
    padding: 10px;
}

button {
    width: 40%;
    height: 50px;
    margin: 20px;
    cursor: pointer;
}